export class SeguimientoPedidoDto {
    seg_id?: number;
    seg_transportadora: string;
    seg_numero_seguimiento: string;
    usu_id: number;
    ped_id: number;

    constructor(
        seg_transportadora: string, 
        seg_numero_seguimiento: string, 
        usu_id: number, 
        ped_id: number,  
        seg_id?: number) {
        this.seg_id = seg_id;
        this.seg_transportadora = seg_transportadora;
        this.seg_numero_seguimiento = seg_numero_seguimiento;
        this.usu_id = usu_id
        this.ped_id = ped_id
    }
}