import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { CategoriaDto } from 'src/app/models/categoria/categoria.dto';
import { SeccionDto } from 'src/app/models/seccion/seccion.dto';
import { CategoriaService } from 'src/app/shared/service/productos/categoria/categoria.service';
import { ProductosService } from 'src/app/shared/service/productos/productos.service';
import { SeccionService } from 'src/app/shared/service/productos/seccion/seccion.service';
import { productDB } from 'src/app/shared/tables/product-list';
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss']
})
export class ProductListComponent implements OnInit {

  urlApi = environment.apiBaseUrl

  productoForm: FormGroup;

  searchTerm = '';
  product_list: any[] = [];

  //DTO PARA SECCION
  seccionDto: SeccionDto[];
  categoriaDto: CategoriaDto[]

  listaVacia: any = undefined;

  public page = 1
  public pageSize = 10

  constructor(
    private fb: FormBuilder,
    private productoService: ProductosService,
    private seccionService: SeccionService,
    private categoriaService: CategoriaService,
    private router: Router
  ) {
    this.productoForm = this.fb.group({
      prod_nombre: [''],
      seccion: [''],
      categoria: [''],
    });
  }

  ngOnInit(): void {
    this.listarProductos();
    this.listarSecciones();
  }

  //Metodo listar todos los productos
  listarProductos() {
    this.productoService.getAllProductos().subscribe(
      (data: any) => {
        this.product_list = data.map((product: any) => {
          return {
            ...product,
            img_url: product.imagen_producto && product.imagen_producto.length > 0 ? product.imagen_producto[0].imagen_url : 'path/to/default/image.jpg'
          };
        });
        console.log(this.product_list)

      },
      err => {
        this.listaVacia = err.error.message;
      }
    );
  }

  //Peticion listar secciones
  listarSecciones(): void {
    this.seccionService.listaSeccion().subscribe(
      data => {
        this.seccionDto = data

      },
      err => {
        err.error.message
      }
    )
  }

  //Peticion cargar categorias por seccion id
  onSectionChangeCategoria() {
    const seccionId = this.productoForm.get('seccion')?.value;
    this.clearSelects()
    this.categoriaService.listaCategoriasBySeccion(seccionId).subscribe(
      data => {
        this.categoriaDto = data
      },
      err => {
        err.error.message
      }
    )
  }

  //Asignar los selects vacios al seleccionar una seccion
  clearSelects() {
    this.productoForm.get('categoria').setValue("");
  }


  //Filtro de productos
  filterProductos() {
    const prod_nombre = this.productoForm.get('prod_nombre')?.value;
    const seccionId = this.productoForm.get('seccion')?.value;
    const categoriaId = this.productoForm.get('categoria')?.value;
    this.productoService.getProductosFilter(prod_nombre, seccionId, categoriaId).subscribe(
      (data: any) => {
        this.product_list = data.map((product: any) => {
          return {
            ...product,
            img_url: product.imagen_producto && product.imagen_producto.length > 0 ? product.imagen_producto[0].imagen_url : 'path/to/default/image.jpg'
          };
        }); // Verifica los datos en la consola
        this.listaVacia = undefined;
      },
      err => {
        this.listaVacia = err.error.message;
        this.product_list = []
        console.log(err.error.message)
      }
    );
  }

  //Metodo guardar id_producto al localStorage
  editarProducto(prod_id: number) {
    localStorage.setItem('productoId', prod_id.toString());
    this.router.navigate(['/productos/editar-producto']);
  }

  
  // Sumar la cantidad total de inventarios de un producto
  getTotalCantidadDisponible(product: any): number {
    if (!product.inventario || product.inventario.length === 0) {
      return 0; // Si no hay inventarios, devuelve 0
    }

    // Sumar todas las cantidades disponibles
    return product.inventario.reduce((total: number, inv: any) => total + inv.inv_cantidad_disponible, 0);
  }


  //Metodo eliminar
  deleteProducto(prod_id: number): void {
    Swal.fire({
      title: '¿Estás seguro de eliminar este producto?',
      text: 'Esta acción es irreversible.',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#fc5c04',
      cancelButtonColor: '#000',
      confirmButtonText: 'Sí, eliminar',
      cancelButtonText: 'Cancelar'
    }).then((result) => {
      if (result.isConfirmed) {
        this.productoService.eliminarProducto(prod_id)
          .subscribe(
            (response) => {
              // Manejar respuesta exitosa (eliminación exitosa)
              Swal.fire({
                title: '¡Producto eliminado!',
                text: response.mensaje,
                icon: 'success',
                confirmButtonColor: '#fc5c04'
              });
              // Actualizar la lista de productos en la vista
              this.listarProductos();
            },
            (error) => {
              let errorMessage = '';
              //Verificar si el mensaje error es un array de lo contrario solo accedemos a el sin array
              if (error.error && error.error.message) {
                if (Array.isArray(error.error.message)) {
                  errorMessage = error.error.message[0];
                } else {
                  errorMessage = error.error.message;
                }
              }
              Swal.fire({
                title: 'Error al eliminar el producto',
                text: errorMessage,
                icon: 'error',
                confirmButtonColor: '#fc5c04'
              });
            }
          );
      } else if (result.dismiss === Swal.DismissReason.cancel) {
        Swal.fire({
          title: 'Cancelado',
          text: 'Producto a salvo',
          icon: 'error',
          confirmButtonColor: '#fc5c04'  // Cambia el color del botón "OK" cuando se cancela
        });
      }
    });
  }
}
