import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PagosService {

  private pagosUrl = environment.pagos_URL

  constructor(private httpClient: HttpClient) { }

  // Solicitar el total de pagos por mes
  getTotalPagosMes(): Observable<number> {
    return this.httpClient.get<number>(`${this.pagosUrl}total/mes`);
  }

  // Solicitar el total de pagos
  getTotalPagos(): Observable<number> {
    return this.httpClient.get<number>(`${this.pagosUrl}total`);
  }


  //Solicitar todos los pagos realizados
  getAllPagos(): Observable<any[]> {
    return this.httpClient.get<any[]>(this.pagosUrl)
  }

}
