import { Injectable, HostListener, Inject } from '@angular/core';
import { BehaviorSubject, Observable, Subscriber } from 'rxjs';
import { WINDOW } from "./windows.service";
import { TokenService } from './token.service';
// Menu
export interface Menu {
	path?: string;
	title?: string;
	icon?: string;
	type?: string;
	badgeType?: string;
	badgeValue?: string;
	active?: boolean;
	bookmark?: boolean;
	children?: Menu[];
}

@Injectable({
	providedIn: 'root'
})

export class NavService {

	public screenWidth: any
	public collapseSidebar: boolean = false

	constructor(
		@Inject(WINDOW) private window,
		private tokenService: TokenService) {
		this.onResize();
		if (this.screenWidth < 991) {
			this.collapseSidebar = true
		}

		// Inicializamos los items del menú basados en el rol del usuario
		this.initializeMenu();
	}

	// Windows width
	@HostListener("window:resize", ['$event'])
	onResize(event?) {
		this.screenWidth = window.innerWidth;
	}

	MENUITEMS: Menu[] = []
	// Array
	items = new BehaviorSubject<Menu[]>(this.MENUITEMS);

	// Método para inicializar el menú
	initializeMenu() {
		// Inicializar el arreglo de menú
		this.MENUITEMS = [];

		// Si el usuario es administrador
		if (this.tokenService.isAdmin()) {
			this.addAdminMenu();
		}
		// Si el usuario es vendedor
		else if (this.tokenService.isVendedor()) {
			this.addVendedorMenu();
		}
		// Si el usuario es supervisor
		else if (this.tokenService.isSupervisor()) {
			this.addSupervisorMenu();
		}
		// Si el usuario es soporte
		else if (this.tokenService.isSoporte()) {
			this.addSoporteMenu();
		}

		// Actualizamos los items del menú
		this.items.next(this.MENUITEMS);
	}

	// Métodos para añadir secciones específicas según el rol
	addAdminMenu() {
		this.addCommonMenuSections();
		this.MENUITEMS.push(
			{
				title: 'Usuarios', icon: 'user-plus', type: 'sub', active: false, children: [
					{ path: '/users/list-user', title: 'Lista de Usuarios', type: 'link' },
					{ path: '/users/create-user', title: 'Crear Usuario', type: 'link' },
				]
			}
		);
	}

	addVendedorMenu() {
		this.addCommonMenuSections();
	}

	addSupervisorMenu() {
		this.addCommonMenuSections();
	}

	addSoporteMenu() {
		this.addCommonMenuSections();
		this.MENUITEMS.push(
			{
				title: 'Usuarios', icon: 'user-plus', type: 'sub', active: false, children: [
					{ path: '/users/list-user', title: 'Lista de Usuarios', type: 'link' },
					{ path: '/users/create-user', title: 'Crear Usuario', type: 'link' },
				]
			}
		);
	}

	// Método para agregar secciones comunes a todos los roles
	addCommonMenuSections() {
		this.MENUITEMS.push(
			{
				path: '/dashboard/default', title: 'Panel', icon: 'home', type: 'link', badgeType: 'primary', active: false
			},
			{
				title: 'Productos', icon: 'box', type: 'sub', active: false, children: [
					{ path: '/productos/seccion', title: 'Seccion', type: 'link' },
					{ path: '/productos/categoria', title: 'Categoria', type: 'link' },
					{ path: '/productos/sub-categoria', title: 'Sub Categoria', type: 'link' },
					{ path: '/productos/producto-lista', title: 'Lista de Productos', type: 'link' },
					{ path: '/productos/agregar-producto', title: 'Agregar Producto', type: 'link' },
				]
			},
			{
				title: 'Ventas', icon: 'dollar-sign', type: 'sub', active: false, children: [
					{ path: '/ventas/transacciones', title: 'Transacciones', type: 'link' },
				]
			},
			{
				title: 'Segumiento Pedido', icon: 'align-left', type: 'sub', active: false, children: [
					{ path: '/seguimiento-pedido/list-seguimiento-pedido', title: 'Pedidos Pedientes', type: 'link' },
					{ path: '/seguimiento-pedido/pedido-asignado', title: 'Pedidos Asignados', type: 'link' },
				]
			},
			{
				title: 'Cupones', icon: 'tag', type: 'sub', active: false, children: [
					{ path: '/coupons/list-coupons', title: 'Lista de Cupones', type: 'link' },
					{ path: '/coupons/create-coupons', title: 'Crear Cupón', type: 'link' },
				]
			},
			{
				title: 'Configuración', icon: 'settings', type: 'sub', children: [
					{ path: '/settings/profile', title: 'Perfil', type: 'link' },
				]
			},
			{
				title: 'Pedidos', path: '/pedidos', icon: 'archive', type: 'link', active: false
			}
		);
	}


}
