import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { SeguimientoPedidoDto } from 'src/app/models/seguimiento_pedido/seguimiento_pedido.dto';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class SeguimientoPedidoService {

  private seguimientoPedidoUrl = environment.seguimientoPedido_URL

  constructor(private httpClient: HttpClient) { }

  // Método para asignar el seguimiento del pedido
  asignarSeguimientoPedido(seguimientoPedidoDto: SeguimientoPedidoDto): Observable<any> {
    return this.httpClient.post(`${this.seguimientoPedidoUrl}`, seguimientoPedidoDto);
  }

}
