import { Component, OnInit } from '@angular/core';
import { FormGroup, UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ModalDismissReasons, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { PedidoDto } from 'src/app/models/pedidos/pedido.dto';
import { SeguimientoPedidoDto } from 'src/app/models/seguimiento_pedido/seguimiento_pedido.dto';
import { PedidosService } from 'src/app/shared/service/pedidos/pedidos.service';
import { SeguimientoPedidoService } from 'src/app/shared/service/pedidos/seguimiento-pedido.service';
import { TokenService } from 'src/app/shared/service/token.service';

@Component({
  selector: 'app-pedido-asignado',
  templateUrl: './lista-pedido-asignados.component.html',
  styleUrls: ['./lista-pedido-asignados.component.scss']
})
export class ListaPedidoAsignado implements OnInit {

  modalRef: NgbModalRef;
  public closeResult: string;

  public estadoPedidoForm: UntypedFormGroup;
  pedidoForm: FormGroup;

  public selected = [];

  seguimientoPedidoDto: SeguimientoPedidoDto[];
  //Dto Pedidos
  pedidoDto: PedidoDto[];
  //LISTA VACIA
  listaVacia: any = undefined

  usu_id: number
  ped_id: number

  public searchText;


  public page = 1
  public pageSize = 10


  constructor(
    private fb: UntypedFormBuilder,
    private modalService: NgbModal,
    private pedidoService: PedidosService,
    private toastrService: ToastrService,
    private tokenService: TokenService,
    private seguimientoPedidoService: SeguimientoPedidoService,
    private router: Router
  ) {
    {
      this.estadoPedidoForm = this.fb.group({
        ped_estado: ['', Validators.required],
      });

      //PedidoForm para buscar
      this.pedidoForm = this.fb.group({
        ped_numero_orden: [''],
      });
    }
  }

  ngOnInit() {
    this.getAllPedidosEnCamino();
  }



  //Abrir Modal
  open(content, ped_id: number) {
    this.modalRef = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
    this.modalRef.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
    this.ped_id = ped_id
  }


  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  //Listar los pedidos
  getAllPedidosEnCamino() {
    this.pedidoService.getAllPedidosEnCamino().subscribe(
      data => {
        this.pedidoDto = data
        this.listaVacia = undefined
      },
      err => {
        this.listaVacia = err.error.message
      }
    )
  }


  //Obtener pedido por numero de orden
  filterPedidos() {
    const ped_numero_orden = this.pedidoForm.get('ped_numero_orden')?.value;

    if (ped_numero_orden) {
      this.pedidoService.getAllPedidosEnCaminoNumeroOrden(ped_numero_orden).subscribe(
        data => {
          if (!data) { // Verificar si la lista está vacía
            this.listaVacia = 'No se encontraron pedidos con ese número de orden';
            this.pedidoDto = []; // Limpiar la lista de pedidos
          } else {
            this.pedidoDto = data;
            this.listaVacia = undefined; // Limpiar el mensaje de error
          }
        },
        err => {
          this.listaVacia = err.error.message;
        }
      );
    } else {
      this.getAllPedidosEnCamino(); // Si el campo de búsqueda está vacío, cargar todos los pedidos.
    }
  }

  //Cambiar el estado del pedido:
  onSubmit(): void {
    const ped_estado = this.estadoPedidoForm.get('ped_estado')?.value;

    if (this.estadoPedidoForm.invalid) {
      this.toastrService.warning('Debes seleccionar un estado para actualizar', 'Érror', {
        timeOut: 3000,
        positionClass: 'toast-top-center',
      });
    } else {
      // Llamar al servicio para actualizar el estado del pedido
      this.pedidoService.updatePedidoEstado(this.ped_id, ped_estado).subscribe(
        response => {
          // Mostrar un mensaje de éxito
          this.toastrService.success('El pedido ha cambiado su estado', 'Éxito', {
            timeOut: 3000,
            positionClass: 'toast-top-center',
          });
          this.modalRef.close();
          this.getAllPedidosEnCamino();
        },
        error => {
          console.error('Error al actualizar el estado del pedido', error);
        }
      );
    }
  }

}
