import { DecimalPipe } from '@angular/common';
import { Component, OnInit, QueryList, ViewChildren } from '@angular/core';
import { FormGroup, UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ModalDismissReasons, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';
import { PedidoDto } from 'src/app/models/pedidos/pedido.dto';
import { SeguimientoPedidoDto } from 'src/app/models/seguimiento_pedido/seguimiento_pedido.dto';
import { NgbdSortableHeader, SortEvent } from 'src/app/shared/directives/NgbdSortableHeader';
import { PedidosService } from 'src/app/shared/service/pedidos/pedidos.service';
import { SeguimientoPedidoService } from 'src/app/shared/service/pedidos/seguimiento-pedido.service';
import { TableService } from 'src/app/shared/service/table.service';
import { TokenService } from 'src/app/shared/service/token.service';
import { ListPagesDB } from 'src/app/shared/tables/list-pages';
import { MENUDB, MenuDB } from 'src/app/shared/tables/menu';

@Component({
  selector: 'app-list-seguimiento-pedido',
  templateUrl: './list-seguimiento-pedido.component.html',
  styleUrls: ['./list-seguimiento-pedido.component.scss'],
  providers: [TableService, DecimalPipe]
})
export class ListSeguimientoPedidoComponent implements OnInit {

  modalRef: NgbModalRef;
  public closeResult: string;

  public asignarPedidoForm: UntypedFormGroup;
  pedidoForm: FormGroup;

  public selected = [];

  seguimientoPedidoDto: SeguimientoPedidoDto[];
  //Dto Pedidos
  pedidoDto: PedidoDto[];
  //LISTA VACIA
  listaVacia: any = undefined

  usu_id: number
  ped_id: number

  public tableItem$: Observable<ListPagesDB[]>;
  public searchText;
  total$: Observable<number>;

  public page = 1
  public pageSize = 10

  //VARIABLES PARA TRANSPORTAR EL DTO
  seg_transportadora: string;
  seg_numero_seguimiento: string


  constructor(
    private fb: UntypedFormBuilder,
    private modalService: NgbModal,
    private pedidoService: PedidosService,
    private toastrService: ToastrService,
    private tokenService: TokenService,
    private seguimientoPedidoService: SeguimientoPedidoService,
    private router: Router
  ) {
    {
      this.asignarPedidoForm = this.fb.group({
        seg_transportadora: ['', Validators.required],
        seg_numero_seguimiento: ['', Validators.required],
      });

      //PedidoForm para buscar
      this.pedidoForm = this.fb.group({
        ped_numero_orden: [''],
      });
    }
  }


  ngOnInit() {
    this.getAllPedidosPendientes();
    this.usu_id = this.tokenService.getUserId();
  }

  //Abrir Modal
  open(content, ped_id: number) {
    this.modalRef = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
    this.modalRef.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
    this.ped_id = ped_id
  }


  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  //Listar los pedidos
  getAllPedidosPendientes() {
    this.pedidoService.getAllPedidosPendiente().subscribe(
      data => {
        this.pedidoDto = data
        this.listaVacia = undefined
      },
      err => {
        this.listaVacia = err.error.message
      }
    )
  }


  //Obtener pedido por numero de orden
  filterPedidos() {
    const ped_numero_orden = this.pedidoForm.get('ped_numero_orden')?.value;

    console.log(ped_numero_orden)
    if (ped_numero_orden) {
      this.pedidoService.getAllPedidosPendienteNumeroOrden(ped_numero_orden).subscribe(
        data => {
          if (!data) { // Verificar si la lista está vacía
            this.listaVacia = 'No se encontraron pedidos con ese número de orden';
            this.pedidoDto = []; // Limpiar la lista de pedidos
          } else {
            this.pedidoDto = data;
            this.listaVacia = undefined; // Limpiar el mensaje de error
          }
        },
        err => {
          this.listaVacia = err.error.message;
        }
      );
    } else {
      this.getAllPedidosPendientes(); // Si el campo de búsqueda está vacío, cargar todos los pedidos.
    }
  }


  // Registrar Asignación de Pedido
  onSubmit(): void {
    if (this.asignarPedidoForm.valid) {
      // Crear un objeto FormData para enviar al backend

      this.seg_transportadora = this.asignarPedidoForm.get('seg_transportadora').value
      this.seg_numero_seguimiento = this.asignarPedidoForm.get('seg_numero_seguimiento').value

      const seguimientoPedidoDto = new SeguimientoPedidoDto(
        this.seg_transportadora,
        this.seg_numero_seguimiento,
        this.usu_id,
        this.ped_id
      )

      // Llamada al servicio para enviar el FormData al backend
      this.seguimientoPedidoService.asignarSeguimientoPedido(seguimientoPedidoDto).subscribe(
        response => {
          // Mostrar un mensaje de éxito
          this.toastrService.success('El seguimiento del pedido se ha asignado correctamente', 'Éxito', {
            timeOut: 3000,
            positionClass: 'toast-top-center',
          });
          // Cerrar el modal si es necesario
          this.modalRef.close();
          this.router.navigate['/seguimiento-pedido/pedido-asignado']
        },
        err => {
          // Mostrar un mensaje de error
          this.toastrService.error('Ocurrió un error al asignar el seguimiento del pedido.', 'Error', {
            timeOut: 3000,
            positionClass: 'toast-top-center',
          });
        }
      );
    } else {
      this.toastrService.error('Por favor complete todos los campos.', 'Error', {
        timeOut: 3000,
        positionClass: 'toast-top-center',
      });
    }
  }


}
