import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ModalDismissReasons, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { ChangePasswordDto } from 'src/app/models/usuario/change-password.dto';
import { LoginUsuarioDto } from 'src/app/models/usuario/login-usuario.dto';
import { UsuarioEmailDto } from 'src/app/models/usuario/usuario-email.dto';
import { AuthService } from 'src/app/shared/service/auth.service';
import { NavService } from 'src/app/shared/service/nav.service';
import { TokenService } from 'src/app/shared/service/token.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  public closeResult: string;

  usuario: LoginUsuarioDto = null;

  modalRef: NgbModalRef;

  usu_nombreUsuario: string;
  usu_password: string;


  //Variables modal olvide password
  resetPasswordForm: FormGroup;
  codeSent: boolean = false;

  email_pass: string

  tittle_pass: string = 'Artesanias Lumar'
  info_pass: string = 'Ingresa tu correo electrónico para solicitar cambio de contraseña'

  usuario_passwordDto: ChangePasswordDto = null
  usuario_emailDto_pass: UsuarioEmailDto = null

  codigo_usuario: string
  new_password_usuario: string

  //Variables modal olvide usuario
  userNameForm: FormGroup;
  info_user: string = 'Ingresa tu correo electrónico para solicitar tu usuario'

  email: string

  usuario_emailDto: UsuarioEmailDto = null

  constructor(
    private fb_user: FormBuilder,
    private fb_password: FormBuilder,
    private authService: AuthService,
    private tokenService: TokenService,
    private toastrService: ToastrService,
    public navServices: NavService,
    private router: Router,
    private modalService: NgbModal,
  ) {
    //Form Olvide user
    this.userNameForm = this.fb_user.group({
      email: ['', [Validators.required, Validators.email]],
    });

    //Form Olvide Password
    this.resetPasswordForm = this.fb_password.group({
      email: ['', [Validators.required, Validators.email]],
      resetPasswordCode: ['', [Validators.required]],
      newPassword: ['', [Validators.required]]
    });
  }

  title: string = "Panel Administración  Artesanias Lumar";
  desc: string = "Administre su tienda de forma eficiente y sencilla. Gestione inventarios, actualice productos y supervise ventas desde un único lugar.";



  ngOnInit() {
  }


  onSubmit(): void {
    if (!this.codeSent) {
      this.sendResetCode();
    } else {
      this.resetPassword();
    }
  }

  //Metodo para enviar el codigo de reestablecer contraseña
  sendResetCode(): void {
    this.email_pass = this.resetPasswordForm.get('email')?.value;

    if (!this.email_pass) {
      this.toastrService.error('Por favor, ingrese un correo electrónico válido.', 'Error', {
        timeOut: 3000,
        positionClass: 'toast-top-center',
      });
      console.log(this.resetPasswordForm.value)
      return;
    }


    this.usuario_emailDto_pass = new UsuarioEmailDto(this.email_pass);


    this.authService.requestPassword(this.usuario_emailDto_pass).subscribe(
      data => {
        this.codeSent = true;
        this.info_pass = 'Verifica el código enviado al correo electrónico para cambiar la contraseña, el código expira en 10 minutos';
        this.toastrService.success('Se envió un código a tu correo electrónico', 'Éxito', {
          timeOut: 3000,
          positionClass: 'toast-top-center',
        });
      },
      err => {
        this.toastrService.error(err.error.message, 'Error', {
          timeOut: 3000,
          positionClass: 'toast-top-center',
        });
      }
    );
  }


  //Metodo reestablecer contraseña con el codigo proporcionado
  resetPassword(): void {
    this.codigo_usuario = this.resetPasswordForm.get('resetPasswordCode')?.value
    this.new_password_usuario = this.resetPasswordForm.get('newPassword')?.value

    this.usuario_passwordDto = new ChangePasswordDto(this.codigo_usuario, this.new_password_usuario)

    if (!this.codigo_usuario || !this.new_password_usuario) {
      this.toastrService.error('Por favor, ingresa todos los datos.', 'Error', {
        timeOut: 3000,
        positionClass: 'toast-top-center',
      });
      return;
    }

    this.authService.changePassword(this.usuario_passwordDto).subscribe(
      data => {
        this.toastrService.success('Contraseña cambiada exitosamente', 'Éxito', {
          timeOut: 3000,
          positionClass: 'toast-top-center',
        });
        this.modalRef.close();
      },
      error => {
        const errorMessage = error.error?.message;
        this.toastrService.error(errorMessage, 'Error', {
          timeOut: 3000,
          positionClass: 'toast-top-center',
        });
      }
    );
  }


  //Metodo para solicitar mi nombre de usuario
  sendUserName(): void {
    this.email = this.userNameForm.get('email')?.value;

    if (!this.email) {
      this.toastrService.error('Por favor, ingrese un correo electrónico válido.', 'Error', {
        timeOut: 3000,
        positionClass: 'toast-top-center',
      });
      return;
    }


    this.usuario_emailDto = new UsuarioEmailDto(this.email);


    this.authService.sendUserName(this.usuario_emailDto).subscribe(
      data => {
        this.toastrService.success('Se envió tu nombre de usuario a tu correo electrónico', 'Éxito', {
          timeOut: 3000,
          positionClass: 'toast-top-center',
        });
        this.modalRef.close();
      },
      err => {
        this.toastrService.error(err.error.message, 'Error', {
          timeOut: 3000,
          positionClass: 'toast-top-center',
        });
      }
    );
  }


  //Abrir Modal
  open(content) {
    this.modalRef = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
    this.modalRef.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }


  onLogin(): void {
    this.usuario = new LoginUsuarioDto(this.usu_nombreUsuario, this.usu_password);
    this.authService.login(this.usuario).subscribe(
      data => {
        if (!data.token) {
          this.toastrService.error(data.response.message, 'Error', {
            timeOut: 3000, positionClass: 'toast-top-center',
          });
        } else {
          this.tokenService.setToken(data.token);
          this.router.navigate(['/dashboard/default']);
          this.navServices.initializeMenu();
        }

      },
      err => {
        this.toastrService.error(err.error.message, 'Error', {
          timeOut: 3000, positionClass: 'toast-top-center',
        });
      }
    );
  }



}
