import { DecimalPipe } from '@angular/common';
import { Component, OnInit, QueryList, ViewChildren } from '@angular/core';
import { Observable } from 'rxjs';
import { NgbdSortableHeader, SortEvent } from 'src/app/shared/directives/NgbdSortableHeader';
import { TableService } from 'src/app/shared/service/table.service';
import { InvoiceDB, INVOICEDB } from '../../shared/tables/invoice';
import { ModalDismissReasons, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { PedidosService } from 'src/app/shared/service/pedidos/pedidos.service';
import { PedidoDto } from 'src/app/models/pedidos/pedido.dto';
import { FormBuilder, FormGroup, UntypedFormGroup, Validators } from '@angular/forms';
import { DetallePedidosService } from 'src/app/shared/service/pedidos/detalle-pedidos.service';
import { error } from 'console';
import { environment } from 'src/environments/environment';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-invoice',
  templateUrl: './invoice.component.html',
  styleUrls: ['./invoice.component.scss'],
  providers: [TableService, DecimalPipe]
})
export class InvoiceComponent implements OnInit {

  modalRef: NgbModalRef;
  public closeResult: string;

  pedidoForm: FormGroup;

  public estadoPedidoForm: UntypedFormGroup;

  urlApi = environment.apiBaseUrl

  //Dto Pedidos
  pedidoDto: PedidoDto[];
  //LISTA VACIA
  listaVacia: any = undefined

  detalle_pedidoDto: any[] = [];

  public page = 1
  public pageSize = 10

  ped_id: number


  constructor(
    private fb: FormBuilder,
    private modalService: NgbModal,
    private pedidoService: PedidosService,
    private detallePedidoService: DetallePedidosService,
    private toastrService: ToastrService,
  ) {
    this.estadoPedidoForm = this.fb.group({
      ped_estado: ['', Validators.required],
    });


    this.pedidoForm = this.fb.group({
      ped_numero_orden: [''],
    });
  }


  //Solicitar el detalle pedido por id
  listarDetallePedido(ped_id: number) {
    this.detallePedidoService.getDetallePedido(ped_id).subscribe(
      data => {
        this.detalle_pedidoDto = data
      },
      err => {
        console.log(err.error.message)
      });
  }


  //Abrir Modal
  open(content, ped_id: number) {
    this.modalRef = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
    //Solicitar el detalle de ese pedido
    this.listarDetallePedido(ped_id)
    this.modalRef.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  //Abrir Modal Editar
  openEdit(content, ped_id: number) {
    this.modalRef = this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
    //Solicitar el detalle de ese pedido
    this.ped_id = ped_id
    this.modalRef.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }




  ngOnInit() {
    this.getAllPedidos();
  }

  //Listar los pedidos
  getAllPedidos() {
    this.pedidoService.getAllPedidos().subscribe(
      data => {
        this.pedidoDto = data
        this.listaVacia = undefined
      },
      err => {
        this.listaVacia = err.error.message
      }
    )
  }


  //Obtener pedido por numero de orden
  filterPedidos() {
    const ped_numero_orden = this.pedidoForm.get('ped_numero_orden')?.value;

    if (ped_numero_orden) {
      this.pedidoService.getAllPedidosByOrden(ped_numero_orden).subscribe(
        data => {
          if (data.length === 0) { // Verificar si la lista está vacía
            this.listaVacia = 'No se encontraron pedidos con ese número de orden';
            this.pedidoDto = []; // Limpiar la lista de pedidos
          } else {
            this.pedidoDto = data;
            this.listaVacia = undefined; // Limpiar el mensaje de error
          }
        },
        err => {
          this.listaVacia = err.error.message;
        }
      );
    } else {
      this.getAllPedidos(); // Si el campo de búsqueda está vacío, cargar todos los pedidos.
    }
  }


  //Cambiar el estado del pedido:
  onSubmit(): void {
    const ped_estado = this.estadoPedidoForm.get('ped_estado')?.value;
    
    if (this.estadoPedidoForm.invalid) {
      this.toastrService.warning('Debes seleccionar un estado para actualizar', 'Érror', {
        timeOut: 3000,
        positionClass: 'toast-top-center',
      });
    } else {
      // Llamar al servicio para actualizar el estado del pedido
      this.pedidoService.updatePedidoEstado(this.ped_id, ped_estado).subscribe(
        response => {
          // Mostrar un mensaje de éxito
          this.toastrService.success('El pedido ha cambiado su estado', 'Éxito', {
            timeOut: 3000,
            positionClass: 'toast-top-center',
          });
          this.modalRef.close();
          this.getAllPedidos();
        },
        error => {
          console.error('Error al actualizar el estado del pedido', error);
        }
      );
    }
  }

}
