import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DetallePedidosService {

  private detallePedidoUrl = environment.detalle_pedido_URL

  constructor(private httpClient: HttpClient) { }

  //Solicitud Listar Detalle Pedido


  //Solicitar la cantidad total de pedidos vendidos por mes
  getCantidadProductosMes(): Observable<number> {
    return this.httpClient.get<number>(`${this.detallePedidoUrl}cantidad/total`);
  }

  //Solicitar el detalle pedido de un pedido
  getDetallePedido(ped_id: number): Observable<any>{
    return this.httpClient.get<any>(`${this.detallePedidoUrl}pedido/detalle/${ped_id}`)
  }
}
