import { DecimalPipe } from '@angular/common';
import { Component, OnInit, QueryList, ViewChildren } from '@angular/core';
import { Observable } from 'rxjs';
import { NgbdSortableHeader, SortEvent } from 'src/app/shared/directives/NgbdSortableHeader';
import { PagosService } from 'src/app/shared/service/pagos/pagos.service';
import { TableService } from 'src/app/shared/service/table.service';
import { TransactionsDB, TRANSACTIONDB } from 'src/app/shared/tables/transactions';

@Component({
  selector: 'app-transactions',
  templateUrl: './transactions.component.html',
  styleUrls: ['./transactions.component.scss'],
  providers: [TableService, DecimalPipe],
})

export class TransactionsComponent implements OnInit {

  //LISTA VACIA
  listaVacia: any = undefined

  public page = 1
  public pageSize = 10

  pagosDto: any[] = []

  constructor(
    private pagosServices: PagosService
  ) { }

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;


  //Solicitar obtener todos los pagos
  getAllPagos() {
    this.pagosServices.getAllPagos().subscribe(
      data => {
        this.pagosDto = data
        this.listaVacia = undefined
      },
      err => {
        this.listaVacia = err.error.message
      }
    )
  }

  ngOnInit() {
    this.getAllPagos();
  }

}
