import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ProductosService {

  private productoUrl = environment.productoURL

  constructor(private httpClient: HttpClient) { }

  public createProducto(formData: FormData): Observable<any> {
    return this.httpClient.post(`${this.productoUrl}`, formData);
  }

  //Obtener todos los productos
  public getAllProductos(): Observable<any> {
    return this.httpClient.get<any>(this.productoUrl);
  }

  //Solicitud eliminar Producto
  public eliminarProducto(id: number): Observable<any> {
    return this.httpClient.delete<any>(`${this.productoUrl}${id}`);
  }

  //Solicitud listar productos con filtros, nombre_producto, seccion, categoria
  public getProductosFilter(prod_nombre: string, seccionId: number, categoriaId:number):Observable<any>{
    return this.httpClient.get<any>(`${this.productoUrl}search?prod_nombre=${prod_nombre}&seccionId=${seccionId}&categoriaId=${categoriaId}`)
  }

    //Solicitud obtener un producto por id
    public getOneProducto(prod_id: number): Observable<any> {
      return this.httpClient.get<any>(this.productoUrl + prod_id);
    }

  //Solicitar editar Producto
  updateProduct(id: number, productData: any): Observable<any> {
    return this.httpClient.put(`${this.productoUrl}${id}`, productData);
  }

}
