import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, Observable } from 'rxjs';
import { PedidoDto } from 'src/app/models/pedidos/pedido.dto';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PedidosService {

  private pedidoUrl = environment.pedido_URL

  constructor(private httpClient: HttpClient) { }

  //Solicitar los pedidos
  getAllPedidos(): Observable<PedidoDto[]> {
    return this.httpClient.get<PedidoDto[]>(this.pedidoUrl)
  }

  //Solicitar los ultimos cinco pedidos
  getAllFivePedidos(): Observable<PedidoDto[]> {
    return this.httpClient.get<PedidoDto[]>(this.pedidoUrl + 'ultimos/five')
  }

  // Servicio de Pedidos
  getAllPedidosByOrden(ped_numero_orden: string): Observable<PedidoDto[]> {
    return this.httpClient.get<PedidoDto>(this.pedidoUrl + ped_numero_orden).pipe(
      map(pedido => pedido ? [pedido] : []) // Convierte el objeto en un array
    );
  }

  //Solicitar los pedidos con estado pendiente:
  getAllPedidosPendiente(): Observable<PedidoDto[]> {
    return this.httpClient.get<PedidoDto[]>(this.pedidoUrl + 'estado/envio/pendiente')
  }

  //Solicitar los pedidos con estado pendiente con numero de orden por parametro:
  getAllPedidosPendienteNumeroOrden(ped_numero_orden: string): Observable<PedidoDto[]> {
    return this.httpClient.get<PedidoDto[]>(this.pedidoUrl + `estado/envio/pendiente/${ped_numero_orden}`)
  }

  //Solicitar los pedidos con estado encamino con numero de orden por parametro:
  getAllPedidosEnCaminoNumeroOrden(ped_numero_orden: string): Observable<PedidoDto[]> {
    return this.httpClient.get<PedidoDto[]>(this.pedidoUrl + `estado/envio/encamino/lista/${ped_numero_orden}`)
  }


  //Solicitar los pedidos con estado en camino:
  getAllPedidosEnCamino(): Observable<PedidoDto[]> {
    return this.httpClient.get<PedidoDto[]>(this.pedidoUrl + 'encamino')
  }


  // Método para actualizar el estado del pedido usando PATCH
  updatePedidoEstado(ped_id: number, ped_estado: string): Observable<any> {
    const url = `${this.pedidoUrl}estado/${ped_id}`;
    return this.httpClient.patch(url, { ped_estado: ped_estado });
  }

}
