import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListSeguimientoPedidoComponent } from './lista-segumiento-pedido/list-seguimiento-pedido.component';
import { ListaPedidoAsignado } from './lista-pedido-asignados/lista-pedido-asignados.component';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'list-seguimiento-pedido',
        component: ListSeguimientoPedidoComponent,
        data: {
          title: "Seguimiento Pedido",
          breadcrumb: "Seguimiento Pedido"
        }
      },
      {
        path: 'pedido-asignado',
        component: ListaPedidoAsignado,
        data: {
          title: "Pedidos Asignados",
          breadcrumb: "Pedidos Asignados"
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MenusRoutingModule { }
